export const buff = (Buff) => {
  const res: any[] = []
  Object.keys(Buff).forEach((key) => {
    if (Buff[key]?.length) {
      Buff[key].forEach((range) => {
        res.push({ type: key, range: range })
      })
    }
  })
  return res
}
